.ServiceCard {
  width: 22%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  height: 35rem;
  border-radius: 1rem;
  background-color: rgb(255, 255, 255);
  margin: 2rem;
  transition: all 0.5s ease-in-out;
}

.ServiceCard:hover {
  transform: translateY(-0.3rem);

  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.25);
  transition: all 0.5s ease-in-out;
}

.ImageContainer {
  width: 90%;
  margin: 0;
  padding: 0;
  background: rgb(239, 235, 235) fff;
  overflow: hidden;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
}

.ServiceCard img {
  width: 54%;
  object-fit: cover;
  border-radius: 1rem;
  transition: all 0.5s ease-in-out;
}

.ServiceCard img:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.1);
  transition: all 0.5s ease-in-out;
}

.ServiceName {
  font-size: 2.3rem;
  font-weight: 600;
  height: 10rem;
  text-transform: uppercase;
  color: #000;
  padding-top: 1rem;
  margin: 0.5rem 0 1rem;
  text-align: center;
}

.ServiceDescription {
  font-family: "Belleza", sans-serif important;
  font-size: 1.9rem;
  color: #545454;
  margin: 0rem 0.3rem 1.25rem;
  line-height: 1.1;
  text-align: left;
  margin-top: 2.5rem;
}

.ServiceButton {
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Belleza", sans-serif;
  padding: 0.5rem 1rem;
  background-color: #ffdd02;
  border: none;
  border-radius: 1rem;
  color: #000;
  font-size: 0.82rem;
  font-weight: 600;
  margin: 0.5rem 0 1rem;
  cursor: pointer;
  justify-content: center;
  transition: 0.5s;
}

.ServiceButton:hover {
  background-color: #39b54a;
  transition: 0.5s;
}

@media screen and (max-width: 650px) {
  .ServiceCard {
    width: 85%;
  }

  .ServiceCard img {
    width: 54%;
  }
  .ServiceName {
     height: 15rem;

  }

  .ServiceDescription {
    /* font-size: 1.1rem; */
    display: none;
  }
}

@media screen and (max-width: 1100px) and (min-width: 650px) {
  .ServiceCard {
    width: 45%;
  }
}
